
"use client";

import * as React from "react";
import Image from 'next/image';
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import * as z from "zod";
import { User, Mail, Lock, Landmark } from 'lucide-react';

import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { PasswordStrengthIndicator } from "@/components/password-strength-indicator";
import { useToast } from "@/hooks/use-toast";

// Define validation schema with Zod
const formSchema = z.object({
  username: z.string().min(3, {
    message: "Username must be at least 3 characters.",
  }).max(20, {
    message: "Username must not exceed 20 characters.",
  }),
  email: z.string().email({
    message: "Please enter a valid email address.",
  }),
  password: z.string()
    .min(8, { message: "Password must be at least 8 characters long." })
    .regex(/[a-z]/, { message: "Password must contain at least one lowercase letter." })
    .regex(/[A-Z]/, { message: "Password must contain at least one uppercase letter." })
    .regex(/[0-9]/, { message: "Password must contain at least one number." })
    .regex(/[^a-zA-Z0-9]/, { message: "Password must contain at least one special character." }),
});

type FormData = z.infer<typeof formSchema>;

export default function SignUpPage() {
  const { toast } = useToast();
  const form = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      username: "",
      email: "",
      password: "",
    },
    mode: "onChange", // Validate on change for immediate feedback
  });

  const passwordValue = form.watch("password"); // Watch password field for strength indicator

  // Define a submit handler.
  async function onSubmit(values: FormData) {
    // Simulate API call - Replace with your actual signup logic
    console.log("Form submitted:", values);
    // Note: Rate limiting needs to be implemented on the backend API endpoint.
    // Example: A serverless function handling signup would check request frequency from an IP or user.

    // Simulate success
    await new Promise(resolve => setTimeout(resolve, 1000)); // Simulate network delay

    toast({
      title: "Signup Successful!",
      description: `Welcome, ${values.username}!`,
    });

    // Reset form after successful submission (optional)
    // form.reset();
  }

  return (
    <main className="flex min-h-screen items-center justify-center bg-gradient-to-br from-background to-secondary/50 p-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8 max-w-6xl w-full items-center">
        {/* Left Column: Legal Info and Image */}
        <div className="flex flex-col items-center md:items-start text-center md:text-left space-y-6 p-4">
          <Landmark className="h-16 w-16 text-primary" />
          <h1 className="text-3xl font-bold text-primary">Secure Access for Legal Professionals</h1>
          <p className="text-lg text-muted-foreground">
            AuthZen provides robust, compliant authentication designed for the rigorous demands of the legal industry. Ensure client confidentiality and protect sensitive case data with multi-factor authentication and stringent security protocols.
          </p>
           <div className="relative w-full aspect-video rounded-lg overflow-hidden shadow-lg">
             <Image
               src="https://picsum.photos/600/400"
               alt="Legal Document Security"
               layout="fill"
               objectFit="cover"
               data-ai-hint="legal case law book"
             />
             <div className="absolute inset-0 bg-black/20" /> {/* Optional overlay */}
           </div>
           <p className="text-sm text-muted-foreground italic">
             Safeguarding sensitive legal documents requires top-tier security.
           </p>
        </div>

        {/* Right Column: Signup Form */}
        <Card className="w-full shadow-xl backdrop-blur-sm bg-card/90"> {/* Slightly increased card transparency */}
          <CardHeader>
            <CardTitle className="text-2xl font-bold text-center text-primary">AuthZen Signup</CardTitle>
            <CardDescription className="text-center">Create your secure account</CardDescription>
          </CardHeader>
          <CardContent>
            <Form {...form}>
              <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
                <FormField
                  control={form.control}
                  name="username"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Username</FormLabel>
                      <FormControl>
                        <div className="relative">
                          <User className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                          <Input placeholder="Enter your username" {...field} className="pl-10 bg-input/80" /> {/* Adjusted input background opacity */}
                        </div>
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="email"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Email</FormLabel>
                      <FormControl>
                        <div className="relative">
                          <Mail className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                          <Input type="email" placeholder="Enter your email" {...field} className="pl-10 bg-input/80" /> {/* Adjusted input background opacity */}
                        </div>
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="password"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Password</FormLabel>
                      <FormControl>
                         <div className="relative">
                          <Lock className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground" />
                          <Input type="password" placeholder="Create a password" {...field} className="pl-10 bg-input/80" /> {/* Adjusted input background opacity */}
                        </div>
                      </FormControl>
                      <FormDescription className="text-xs">
                        Min 8 chars, 1 uppercase, 1 lowercase, 1 number, 1 special char.
                      </FormDescription>
                      <FormMessage />
                      <PasswordStrengthIndicator password={passwordValue} />
                    </FormItem>
                  )}
                />
                <Button type="submit" className="w-full bg-primary hover:bg-primary/90 text-primary-foreground" disabled={form.formState.isSubmitting}>
                  {form.formState.isSubmitting ? "Signing up..." : "Sign Up"}
                </Button>
              </form>
            </Form>
          </CardContent>
           <CardFooter className="flex justify-center">
              <p className="text-sm text-muted-foreground">
                Already have an account? <a href="#" className="text-accent hover:underline">Log in</a>
              </p>
           </CardFooter>
        </Card>
      </div>
    </main>
  );
}
