"use client";

import React from 'react';
import { Progress } from '@/components/ui/progress';
import { cn } from '@/lib/utils';

interface PasswordStrengthIndicatorProps {
  password?: string;
}

const calculateStrength = (password: string): number => {
  let strength = 0;
  if (!password) return 0;

  // Length check
  if (password.length >= 8) strength += 25;
  if (password.length >= 12) strength += 15; // Bonus for longer

  // Character type checks
  const hasLowerCase = /[a-z]/.test(password);
  const hasUpperCase = /[A-Z]/.test(password);
  const hasNumbers = /[0-9]/.test(password);
  const hasSymbols = /[^a-zA-Z0-9]/.test(password);

  let typesCount = 0;
  if (hasLowerCase) typesCount++;
  if (hasUpperCase) typesCount++;
  if (hasNumbers) typesCount++;
  if (hasSymbols) typesCount++;

  if (typesCount >= 2) strength += 20;
  if (typesCount >= 3) strength += 20;
  if (typesCount >= 4) strength += 20;

  return Math.min(strength, 100); // Cap at 100
};

// Using CSS variables defined in globals.css for theme consistency
const getStrengthColorClass = (strength: number): string => {
  if (strength < 33) return 'bg-destructive'; // Use destructive color from theme
  if (strength < 66) return 'bg-yellow-500'; // Keep yellow for medium, could be themed too
  return 'bg-green-500'; // Keep green for strong, could be themed too
};

const getStrengthLabel = (strength: number): string => {
  if (strength < 33) return 'Weak';
  if (strength < 66) return 'Medium';
  return 'Strong';
}

const getStrengthLabelColorClass = (strength: number): string => {
    if (strength < 33) return 'text-destructive'; // Use destructive color from theme
    if (strength < 66) return 'text-yellow-600'; // Keep yellow-600 for consistency with progress
    return 'text-green-600'; // Keep green-600 for consistency with progress
}

export const PasswordStrengthIndicator: React.FC<PasswordStrengthIndicatorProps> = ({ password = '' }) => {
  const strength = calculateStrength(password);
  const indicatorColorClass = getStrengthColorClass(strength);
  const label = getStrengthLabel(strength);
  const labelColorClass = getStrengthLabelColorClass(strength);


  return (
    <div className="space-y-1">
      <Progress value={strength} className="h-2 [&>*]:transition-all [&>*]:duration-300" indicatorClassName={indicatorColorClass} />
      <p className={cn("text-xs font-medium", labelColorClass)}>
        Password strength: {label}
      </p>
    </div>
  );
};
